import { FC, Suspense, lazy } from "react"
import { useAppSelector } from "@/app/lib/hooks/redux"
import { PreloaderCard } from "@/shared/Preloader"


export const EventSaleLazy = lazy(() => import('./EventSale'));


export const EventSaleContainer: FC = () => {


    const isSaleFetched = useAppSelector(state => state.eventSale.isFetched)


    return isSaleFetched

        ? <Suspense fallback={<PreloaderCard />}>
            <EventSaleLazy />
        </Suspense>


        : <PreloaderCard />
}

